import { HTMLProps } from 'react';
import { styled } from 'styled-components';

import Icon from '#components/Icon';
import ScreenReaderOnly from '#components/ScreenReaderOnly';

import { WrapperProps, LabelProps } from './Checkbox.types';

// In order to consistently override default appearance we have
// to hide the default input
export const HiddenInput = styled(ScreenReaderOnly).attrs({
  as: 'input',
})`` as React.FC<HTMLProps<HTMLInputElement>>;

export const Label = styled.label<LabelProps>`
  display: flex;
  align-items: center;
  gap: var(--check-margin);
  color: var(--color);
  cursor: ${({ $disabled }) => ($disabled ? 'default' : 'pointer')};
  font: ${({ theme }) => theme.textStyle.formInput.error};
  user-select: none;
`;

export const TickIcon = styled(Icon)`
  position: absolute;
  top: 0;
  left: 0;
`;

export const CheckableBox = styled.div`
  width: var(--width);
  height: var(--height);
  box-sizing: border-box;
  flex-shrink: 0;
  background: var(--check-background-color);

  /* we use box-shadow "inset" so we can render the border inside
   the div, matching the designs */
  box-shadow: 0px 0px 0px 1px var(--check-border-color) inset;
  color: var(--color);

  svg {
    display: none;
  }
`;

export const Wrapper = styled.div<WrapperProps>`
  --width: var(--height);
  --height: ${({ $size }) => {
    switch ($size) {
      case 'small':
        return '16px';
      case 'base':
        return '24px';
      case 'large':
        return '32px';
    }
  }};
  --check-margin: ${({ $size, theme }) => {
    switch ($size) {
      case 'large':
        return `${theme.spacing.x1_5}px`;
      default:
        return `${theme.spacing.x1}px`;
    }
  }};
  --color: ${({ theme }) => theme.color.ink.base};
  --check-border-color: ${({ theme }) => theme.color.divider.black};
  --check-background-color: ${({ theme }) => theme.color.ink.inverted};

  position: relative;
  padding: ${({ theme, $isCosy }) => ($isCosy ? 0 : theme.spacing.x1_5)}px 0;
  background-color: var(--background-color);
  color: var(--color);

  ${({ theme, $disabled }) => {
    if ($disabled) {
      return `
        --color: ${theme.color.semiotic.disabled};
        --check-border-color: ${theme.color.semiotic.disabled};
        --check-background-color: ${theme.color.canvas.secondary};
      `;
    }
  }}

  &:focus,
  &:hover,
  &:active {
    color: var(--color);
  }

  &:focus-within ${CheckableBox} {
    outline: 2px solid ${({ theme }) => theme.color.semiotic.info};
  }

  :checked + ${Label} svg {
    display: initial;
  }
`;
