import React from 'react';
import { useFormContext } from 'react-hook-form';

import { SvgrProps } from '#types/svgr.d';

import Icon from '#components/Icon';

import Tick16Svg from '#assets/icons/tick-solid-16.svg';
import Tick24Svg from '#assets/icons/tick-solid-24.svg';
import Tick32Svg from '#assets/icons/tick-solid-32.svg';

import { CHECKBOX_SIZE_BASE } from './Checkbox.constants';
import { CheckableBox, HiddenInput, Label, Wrapper } from './Checkbox.styles';
import { CheckboxProps, CheckboxSize } from './Checkbox.types';

const TickIconMap: Record<CheckboxSize, SvgrProps> = {
  small: Tick16Svg,
  base: Tick24Svg,
  large: Tick32Svg,
};

const Checkbox: React.FC<CheckboxProps> = ({
  id,
  className,
  controlled = true,
  disabled = false,
  label,
  size = CHECKBOX_SIZE_BASE,
  isCosy = false,
  ...inputProps
}) => {
  const form = useFormContext();

  if (controlled && !form) {
    throw new Error(
      `Checkbox should be wrapped by a FormProvider from @indy/ui/Form, unless the controlled={false} prop is set`,
    );
  }

  const inputRegistration = controlled ? form.register(id) : {};

  return (
    <Wrapper
      className={className}
      $size={size}
      $disabled={disabled}
      $isCosy={isCosy}
    >
      <HiddenInput
        type="checkbox"
        id={id}
        name={id}
        disabled={disabled}
        {...inputProps}
        {...inputRegistration}
      />
      <Label htmlFor={id} $disabled={disabled}>
        <CheckableBox>
          <Icon size={size} svg={TickIconMap[size]} />
        </CheckableBox>
        <div>{label}</div>
      </Label>
    </Wrapper>
  );
};

export default Checkbox;
